
import './App.css';


// import React, { Component } from 'react'
import $ from 'jquery'
import "jquery-ui-dist/jquery-ui";
import { useEffect, useState } from 'react';

import Hommei from './COMPONENTS/Hommei';
import Blogitem from './COMPONENTS/Blogitem';
import { BrowserRouter as Router, Routes, Route} from "react-router-dom"
import Api from './COMPONENTS/Api';
import axios from 'axios'











function App() {

  const[querydata,setquerydata]=useState({
    sendermail:'',
    message:''
  })


   

  
 



  const addClass = async () => {
    console.log("bxhg")
    document.querySelector('nav').classList.add('black');


  }

  const open_dropdown=()=>{
    
    $('.dropdown-contact').toggleClass('none')

  }

  const change_query=(e)=>{
    setquerydata({...querydata,[e.target.name]:e.target.value})

  }

  const submit_query=(e)=>{
    e.preventDefault()
    axios.post('http://localhost:6001/blog/mail', querydata,{
      withCredentials: true
  }).then(async (res) => {
     
         await setquerydata({
          sendermail:'',
    message:''

         })
         $('.dropdown-contact').addClass('none')

   
     
  }).catch((e) => {
      console.log(e)

  })


  }

  useEffect(() => {
    console.log(window.location.pathname)
    $(window).scroll(function () {

      var scr = $(window).scrollTop();
      if (scr >= 180) {
        $(".head,#nava,#navb,#navc,#navd").addClass("black");

      }
      else {
        $(".head,#nava,#navb,#navc,#navd").removeClass("black");

      }
      if(window.innerWidth <= 500){
        if(scr>=50){
          $('#footer').addClass('none')

        }
        else{
          $('#footer').removeClass('none')
  
        }
        
      }
     
    })
    $('#menubtn').click(function(){
      console.log("click");
      $('.sidebar').addClass('open');
    })
    $('.bx-x').click(function(){''
      $('.sidebar').removeClass('open');
    })

    if(window.location.pathname==='/'){
      $(`a`).removeClass('active')
      $(`span #nava`).addClass('active')
    }
    if(window.location.pathname!=='/'){
      $(`a`).removeClass('active')
      $(`span a[href*="${window.location.pathname}"]`).addClass('active')

    }

   
      
   



    

    console.log("sxjx")


  },
    [])
  return (
    <Router>
      

      <div className="App" style={{ width: "100%", display: "flex", flexDirection: "column", minHeight: "100vh" }} onScroll={addClass} >

        <nav className="nav" >
          <div className='head'>
            <span className='dd' ><a href='/' id="nava">Home</a></span>
            <span className='dd' ><a href='/api' id="navb">API</a></span>

            <span className='dd' ><a href='/blog' id="navc">Blogs</a></span>
            <span className='dd ' id='drop-span' ><p  id="navd" class="dropbtn" onClick={open_dropdown}>Content</p>
            <div className="dropdown-contact none">
              <div className="dropdown-content">
                <span><p>Mail id</p> <input type="text" placeholder='contact email' name='sendermail' onChange={change_query} /> </span>
                <span><p>Write your message </p><textarea id="" cols="30" rows="10" placeholder='Write your query' name='message' onChange={change_query}></textarea> </span>
                <span className='btn'><button onClick={submit_query}>Submit</button></span>
              </div>

              </div>
              </span>

          </div>
          <div className="menubutton">
            <button id="menubtn">
              <i class='bx bx-menu'></i>
            </button>

          </div>


        </nav>
        <div className="sidebar">
          <span className='btn'><i class='bx bx-x'></i></span>
          
          <span className='de'><a href='/'>Home</a></span>
        
          <span className='de'><a href='/api' >API</a></span>
          

          <span className='dd'><a href='/blog'>Blogs</a></span>
          
         
        </div>
        {/* content */}

        <Routes>
          <Route path='/' element={<Hommei />} />
          <Route path='/blog' element={<Blogitem />} />
          <Route path='/api' element={<Api/>} />

        </Routes>
        {/*                                            footer                                                    */}
        <div className="footer" id='footer'>
          <div className="socials">
            <span className="social"><i class='bx bxl-linkedin-square'></i></span>
            <span className="social"><i class='bx bxl-facebook-square' ></i></span>
            <span className="social"><i class='bx bxl-gmail' ></i></span>
            <span className="social"><i class='bx bxl-github' ></i></span>
            <span className="social"><i class='bx bxl-instagram' ></i></span>
          </div>

        </div>




      </div>
    </Router>
  );
}

export default App;
