import React from 'react'
import './CSSS/apibox.css'

function Apibox({name,description,url}) {
  return (
    <div className='apibox-main'>
      <div className="apiname">
        {name}
      </div>
          <div className="api-description">
                    {description}

          </div>
          <div className="api-link">
                   {url}

          </div>
      
    </div>
  )
}

export default Apibox
